  -moz-osx-font-smoothing: grayscale;
  background-color: rgba(255, 255, 255, 0.4)!important;
  background-blend-mode: lighten;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#nav_user {
  color:white;
}

.top-nav-collapse {
    background-color: #24355C;
  }
