.Home {
    text-align: center;
  }

  .intro {
    padding:10px;
    background-color: rgba(0, 0, 0, 0.5);
  }

  .Home-logo {
    padding:10px;
    background-color: rgba(0, 0, 0, 0.5);
    -webkit-animation: Home-logo-spin infinite 20s linear;
            animation: Home-logo-spin infinite 20s linear;
    height: 40vmin;
    pointer-events: none;
    border-radius: 40px;  
  }
  
  .Home-header {
    background: url("https://expertwebinarseries.com/wp-content/uploads/2019/11/woman-in-restaurant-wearing-apron-1739748-1800x1200.jpg")no-repeat center center fixed;
      background-size: cover;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
    background-color: rgba(255, 255, 255, 0.4)!important;
    background-blend-mode: lighten;
  }
  
  .Home-link {
    color: #61dafb;
  }
  
  @-webkit-keyframes Home-logo-spin {
    from {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
    to {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
    }
  }
  
  @keyframes Home-logo-spin {
    from {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
    to {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
    }
  }
  
  .Home-details {
    margin-top: 2em;
    font-size: 14px;
    max-width: 33%;
  }
  .Home-details a {
    color: #61dafb;
  }
  .Home-details-links {
    text-align: left;
    display: inline-block;
  }
  
  .card {
    margin-top: 50px;
    background: #eee;
    padding: 3em;
    line-height: 1.5em; }
  
  
